import React, { Component } from 'react';
import Login from './login';
import Chat from './chat';
import client from './feathers';
import _ from 'lodash'

class Application extends Component {
  constructor(props) {
    super(props);

    this.state = {users: []};
  }

  componentDidMount() {
    const messages = client.service('messages');
    const room = client.service('room');

    // Try to authenticate with the JWT stored in localStorage
    client.authenticate().catch(() => this.setState({ login: null }));

    // On successfull login
    client.on('authenticated', login => {
      // Get all users and messages
      Promise.all([
        messages.find({
          query: {
            $sort: { createdAt: -1 },
            $limit: 50
          }
        }),
        room.find()
      ]).then( ([ messagePage, roomUsers ]) => {
        // We want the latest messages but in the reversed order
        const messages = messagePage.data.reverse();
        const users = roomUsers;

        // Once both return, update the state
        this.setState({ login, messages, users });
      });
    });

    // On logout reset all all local state (which will then show the login screen)
    client.on('logout', () => this.setState({
      login: null,
      messages: null,
      users: null
    }));

    // Add new messages to the message list
    messages.on('created', message => this.setState({
      messages: this.state.messages.concat(message)
    }));

    // Add new users to the user list
    room.on('created', user => {
      let newUsers = _.unionBy(this.state.users, [user], 'id');
      this.setState({
        users: newUsers
      });
    });

    room.on('removed', user => {
      this.setState({
        users: _.filter(this.state.users, u => user.id !== u.id)
      });
    });
  }

  render() {
    if(this.state.login === undefined) {
      return <div className="container text-center">
        <h1>Loading...</h1>
      </div>;
    } else if(this.state.login) {
      return <Chat messages={this.state.messages} users={this.state.users} />
    }

    return <Login />;
  }
}

export default Application;
